<template>
  <div class="page">
    <Background />
    <AlertsContainer />
    <Header />
    <div class="structure animated fadeInUp">
      <div class="intro">
        <div class="header">
          <div class="title">Cryptocurrency stacking with a 20% yield</div>
          <p>Make money on a cryptocurrency with minimal risk</p>
          <ul>
            <li><p>Variable minimum deposit</p></li>
            <li><p>Minimum period: 20 hours</p></li>
          </ul>  
        </div>
        <div class="deposit">
        </div>
      </div>
      <div class="actions">
        <div class="calculator">
          <div class="title">Calculate your profit</div>
          <p>How much {{ network }} do you want to send to stacking?</p>
          <div class="wrapper">
            <input disabled class="input" type="text" :value="computedAmount" />
            <div class="currencies">
              <Icon
                @click.native="selectCurrency(0)"
                :class="currency(0)"
                :icon="['currency__icon', 'bitcoin']"
              />
              <Icon
                @click.native="selectCurrency(1)"
                :class="currency(1)"
                :icon="['currency__icon', 'ethereum']"
              />
              <Icon
                @click.native="selectCurrency(2)"
                :class="currency(2)"
                :icon="['currency__icon', 'litecoin']"
              />
            </div>
          </div>
          <input v-model="amount" min="0" max="100" type="range" />
          <p>
            Сalculate your profit for the day, month and year based on the
            amount of the investment
          </p>
        </div>
        <div class="stacking">
          <div class="income">
            <p>Daily income</p>
            <p class="bucks">{{ dailyIncome }}</p>
          </div>
          <div class="divider"></div>
          <div class="income">
            <p>Monthly income</p>
            <p class="bucks">{{ monthlyIncome }}</p>
          </div>
          <div class="divider"></div>
          <div class="income">
            <p>Annual income</p>
            <p class="bucks">{{ annualIncome }}</p>
          </div>
          <Button class="button" value="Begin stacking" />
        </div>
      </div>
      <div class="special-offer">
        <div class="header">
          <div class="title">Are you a large investor?</div>
          <p>For investors with a deposit of $1000 or more we have prepared a special offer that will allow them to earn more and manage their investments through a personal manager.</p>
        </div>
        <div class="inputs">
          <input class="input" type="text" placeholder="E-mail" />
          <input class="input" type="text" placeholder="Your name" />
          <Button class="button" value="Begin stacking" />
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from '@/components/header/Header.vue'
import Footer from '@/components/footer/Footer.vue'
import AlertsContainer from '@/components/partial/AlertsContainer'
import Background from '@/components/views/exchange/Background.vue'
import Button from '@/components/partial/Button.vue'
import Icon from '@/components/partial/Icon.vue'
// import anime from 'animejs'

export default {
  name: 'Contract',
  components: {
    AlertsContainer,
    Header,
    Footer,
    Background,
    Button,
    Icon,
  },
  data: () => ({
    selectedCurrency: 'bitcoin',
    amount: 0,
    currencies: ['bitcoin', 'ethereum', 'litecoin'],
  }),
  computed: {
    network() {
      return { bitcoin: 'BTC', ethereum: 'ETH', litecoin: 'LTC' }[
        this.selectedCurrency
      ]
    },
    computedAmount() {
      return this.amount + ' ' + this.network
    },
    dailyIncome() {
      return ((this.amount * 0.2) / 365).toFixed(3) + ' ' + this.network
    },
    monthlyIncome() {
      return ((this.amount * 0.2) / 12).toFixed(3) + ' ' + this.network
    },
    annualIncome() {
      return (this.amount * 0.2).toFixed(3) + ' ' + this.network
    },
  },
  methods: {
    currency(index) {
      return [this.currencies[index] === this.selectedCurrency ? 'active' : '']
    },
    selectCurrency(index) {
      this.selectedCurrency = this.currencies[index]
    },
  },
  mounted() {},
}
</script>

